@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,700&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    font-family: 'Yaro Op'
}
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #ecf0f1;
}
.login-content {
    background-color: #fff;
    border-radius: 15px;
    width: 960px;
    height: 50%;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
.login-content::before {
    content: "";
    position: absolute;
    background-color: #403F44;
    width: 40%;
    height: 100%;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;


    left: 0;
}
.login-title {
    font-size: 28px;
    font-weight: bold;
    text-transform: capitalize;

}
.login-title-primary {
    color: #fff;
}
.login-title-second {
    color: #49c1ea
}
.login-description {
    font-size: 14px;
    font-weight: 300;
    line-height: 30px;
}
.login-description-primary {
    color: #fff;
}
.login-description-second {
    color: #7f8c8d;
}
.login-btn {
    border-radius: 15px;
    text-transform: uppercase;
    color: #fff;
    font-size: 10px;
    padding: 10px 50px;
    cursor: pointer;
    font-weight: bold;
    width: 180px;
    align-self: center;
    border: none;

}
.login-btn-primary {
    background-color: transparent;
    border: 1px solid #fff;
    transition: background-color .5s;
}
.login-btn-primary:hover {
    background-color: #fff;
    color: #58af9b;
}
.login-btn-second {
    background-color: #58af9b;
    border: 1px solid #58af9b;
    transition: background-color .5s;
}
.login-btn-second:hover {
    background-color: #fff;
    border: 1px solid #58af9b;
    color: #58af9b;
}
.login-first-content {
    display: flex;
}
.login-first-content .login-second-column {
    z-index: 11;
}
.login-first-column {
    text-align: center;
    width: 40%;
    z-index: 10;
}
.login-second-column {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login-social-media {
    margin: 1rem 0;
}
.login-link-social-media:not(:first-child){
    margin-left: 10px;
}
.login-link-social-media .login-item-social-media {
    transition: background-color .5s;
}
.login-link-social-media:hover .login-item-social-media{
    background-color: #58af9b;
    color: #fff;
    border-color: #58af9b;
}
.login-list-social-media {
    display: flex;
    list-style-type: none;
}
.login-item-social-media {
    border-radius: 50%;
    width: 35px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    background-color: #ecf0f1;
    color: #000000;
}
.login-form {
    display: flex;
    flex-direction: column;
    width: 55%;
}
.login-form input {
    height: 45px;
    width: 100%;
    border: none;
    background-color: #ecf0f1;
}



input:-webkit-autofill 
{    
    -webkit-box-shadow: 0 0 0px 1000px #ecf0f1 inset !important;
    -webkit-text-fill-color: #000 !important;
}
.login-label-input {
    display: flex;
    align-items: center;
    margin: 8px;
    background-color: #ecf0f1;

}
.login-icon-modify {
    color: #7f8c8d;
    padding: 0 5px;
}

.icon-custom{
    margin: 5px 5px;
}

/* second content*/

.login-second-content {
    position: absolute;
    display: flex;
}
.login-second-content .login-first-column {
    order: 2;
    z-index: -1;
}
.login-second-content .login-second-column {
    order: 1;
    z-index: -1;
}
.login-password {
    color: #34495e;
    font-size: 14px;
    margin: 15px 0;
    text-align: center;
}
.login-password::first-letter {
    text-transform: capitalize;
}



.login-sign-in-js .login-first-content .login-first-column {
    z-index: -1;
}

.login-sign-in-js .login-second-content .login-second-column {
    z-index: 11;
}
.login-sign-in-js .login-second-content .login-first-column {
    z-index: 13;
}

.login-sign-in-js .login-content::before {
    left: 60%;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    animation: slidein 1s; /*MODIFIQUEI DE 3s PARA 1.3s*/

    z-index: 12;
}

.login-sign-up-js .login-content::before {
    animation: slideout 1s; /*MODIFIQUEI DE 3s PARA 1.3s*/
    z-index: 12;
}

.login-sign-up-js .login-second-content .login-first-column,
.login-sign-up-js .login-second-content .login-second-column {
    z-index: -1;
}

.login-sign-up-js .login-first-content .login-second-column {
    z-index: 11;
}

.login-sign-up-js .login-first-content .login-first-column {
    z-index: 13;    
}


/* DESLOCAMENTO CONTEÚDO ATRÁS DO CONTENT:BEFORE*/
.login-sign-in-js .login-first-content .login-second-column {

    z-index: -1;
    position: relative;
    animation: deslocamentoEsq 1.3s; /*MODIFIQUEI DE 3s PARA 1.3s*/
}

.login-sign-up-js .login-second-content .login-second-column {
    position: relative;
    z-index: -1;
    animation: deslocamentoDir 1.3s; /*MODIFIQUEI DE 3s PARA 1.3s*/
}

