@media screen and (max-width: 1040px) {
    .login-content {
        width: 100%;
        height: 100%;
    }

    .login-content::before {
        width: 100%;
        height: 40%;
        top: 0;
        border-radius: 0;
    }
    .login-first-content, .login-second-content {
        flex-direction: column;
        justify-content: space-around;
    }

    .login-first-column, .login-second-column {
        width: 100%;
    }
    
    .login-sign-in-js .login-content::before {
        top: 60%;
        left: 0;
        border-radius: 0;

    }

